body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, #app {
  height: 100%;
  width: 100%;
  margin: 0;
}

.roulette-page {
  width: 100vw;
  height: 90%;
  min-height: 90%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #181818;
}

.roulette-div-portrait {
  width: 100%;
  height: 90%;
  min-height: 90%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #181818;
}

.roulette-div {
  width: 100%;
  height: 100%;
  min-height: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #181818;
}

.wall-canvas {
  border-style: solid;
  image-rendering: pixelated;
  image-rendering: optimizespeed;
}